@import url(https://fonts.googleapis.com/css2?family=Comfortaa:wght@700&display=swap);
.button{padding:10px 40px;border:2px solid var(--primary-border-color);font-size:var(--font-size-md);border-radius:var(--primary-border-radius)}.button:hover{border-color:var(--tertiary-border-color)}
.auth__form{max-width:800px;width:100%;background-color:var(--secondary-bg-color);padding:20px;border-radius:var(--secondary-border-radius);display:flex;flex-direction:column;justify-content:center;align-items:center}.auth__form-footer{display:flex;flex-direction:column;align-items:center;margin-top:30px}.auth__form-link{display:inline-block;margin-top:30px;color:var(--primary-text-color);position:relative}.auth__form-link::before{position:absolute;content:"";width:0%;right:0;height:2px;bottom:-5px;pointer-events:none;background-color:var(--tertiary-bg-color);border-radius:var(--secondary-border-radius);transition:.5s}.auth__form-link:hover::before{width:100%;left:0}
.field{width:100%;padding:0 10px;position:relative;display:flex;align-items:center;z-index:1}.form__field{width:100%;border:2px solid var(--secondary-border-color);padding:10px 15px;border-radius:var(--primary-border-radius);font-size:var(--font-size-md)}.form__field:-webkit-autofill{box-shadow:inset 0 0 0 1000px var(--secondary-bg-color)}.form__label{position:absolute;left:20px;z-index:-1;transition:.5s;font-size:var(--font-size-md);background-color:var(--primary-bg-color)}.form__field:not(:-ms-input-placeholder).form__field:not(:focus)~.form__label{z-index:1;transform:translateY(-150%);font-size:var(--font-size-sm)}.form__field:focus~.form__label,.form__field:not(:placeholder-shown).form__field:not(:focus)~.form__label{z-index:1;transform:translateY(-150%);font-size:var(--font-size-sm)}
.auth__form-field{margin-top:15px;width:100%}.auth__form-field .form__field{border-color:var(--primary-border-color)}.auth__form-field .form__label{background-color:var(--secondary-bg-color)}
.header{min-height:80px;background-color:var(--secondary-bg-color);display:flex;align-items:center;justify-content:space-between;padding:0 20px}.header__title{font-size:var(--font-size-xl)}.logo{display:flex;align-items:center;justify-content:center}.logo p{margin-left:10px;font-size:var(--font-size-lg)}.header__button:hover path{fill:var(--primary-svg-color)}
.auth__form-header{display:flex;flex-direction:column;align-items:center;margin-bottom:30px}.auth__form-title{margin-top:15px}
.message{position:absolute;top:20px;background-color:var(--danger-bg-color);padding:15px 20px;border-radius:var(--primary-border-radius);perspective:400px;opacity:0;transform-style:preserve-3d;transform:translateY(-50px);animation:show-message 1s forwards;transition:.5s}.message--succes{background-color:var(--succes-bg-color)}@keyframes show-message{100%{transform:none;opacity:1}}
.login{width:100%;min-height:100vh;display:flex;align-items:center;justify-content:center;padding:20px}
.registr{width:100%;min-height:100vh;display:flex;align-items:center;justify-content:center;padding:20px}
.reset-password{width:100%;min-height:100vh;display:flex;align-items:center;justify-content:center;padding:20px}
.checkbox{width:25px;height:25px;border:2px solid var(--tertiary-border-color);display:flex;align-items:center;justify-content:center;border-radius:var(--primary-border-radius);margin-right:10px;cursor:pointer;transition:.5s}.checkbox svg{width:15px;opacity:0}.checkbox:hover{background-color:var(--tertiary-bg-color)}.checkbox:hover svg{opacity:1}.checkbox--active{background-color:var(--tertiary-bg-color)}.checkbox--active svg{opacity:1}
.todos{margin:20px 0;padding:0 10px;flex:1 1 auto;overflow-y:auto;height:100%}.todo{display:flex;align-items:center;background-color:var(--secondary-bg-color);margin-top:15px;padding:15px 10px;border-radius:var(--primary-border-radius);opacity:0;animation:show-todos .5s forwards}.todo:first-child{margin-top:0}.todo__title{font-size:var(--font-size-md);flex:1 1 auto}.todo__button{margin-left:15px}.todo__button:hover path{fill:var(--primary-svg-color)}.todos__form{margin-top:20px}@keyframes show-todos{100%{opacity:1}}
.modal{position:fixed;top:0;right:0;bottom:0;left:0;background-color:rgba(0,0,0,.5);z-index:99;display:flex;align-items:center;justify-content:center;opacity:0;pointer-events:none;padding:20px;transition:.5s}.modal--active{opacity:1;pointer-events:all}.modal--active .modal-content{transform:translate(0)}.modal-content{max-width:800px;width:100%;background-color:var(--primary-bg-color);padding:20px;border-radius:var(--secondary-border-radius);position:relative;transform:translateY(-100px);transition:.5s}.modal__close{position:absolute;width:30px;height:30px;right:10px;top:10px;display:flex;align-items:center;justify-content:center}.modal__close::before,.modal__close::after{position:absolute;content:"";width:100%;height:4px;left:0;background-color:var(--tertiary-bg-color);border-radius:var(--secondary-border-radius);transition:.5s}.modal__close::before{transform:rotate(45deg)}.modal__close::after{transform:rotate(-45deg)}.modal__close:hover::before{transform:rotate(40deg)}.modal__close:hover::after{transform:rotate(-40deg)}
.board{width:100%;display:flex;flex-direction:column;height:100vh;padding-bottom:10px}@media(max-width: 768px){.board{padding-left:10px}}
.folders{margin:10px 0;flex:1 1 auto;height:100%;overflow-y:auto}.folder{padding:15px 10px;background-color:var(--primary-bg-color);margin-top:10px;border-radius:var(--primary-border-radius);position:relative;display:flex;align-items:center;justify-content:space-between;cursor:pointer;opacity:0;animation:show-folders .5s forwards;transition:.5s;overflow:hidden}.folder::before{position:absolute;content:"";width:100%;height:4px;top:0;background-color:var(--tertiary-bg-color);border-radius:var(--primary-border-radius);right:0;opacity:0;transition:.5s}.folder:hover::before{opacity:1}.folder--active::before{opacity:1}.folder__title{font-size:var(--font-size-md)}.folder__buttons{display:flex}.folder__button{margin-left:10px;height:20px;width:20px}.folder__button svg{width:20px;height:20px}.folder__button svg path{fill:var(--secondary-svg-color)}.folder__button:hover path{fill:var(--primary-svg-color)}.folders__title{text-align:center;margin:20px 0}.folders__buttons{display:flex;align-items:center;justify-content:center}.folders__button{border-color:var(--secondary-border-color);margin-right:15px}.folders__button:last-child{margin-right:0}.folders__form{margin-top:20px;display:flex;align-items:center}.folders__form-button{border-color:var(--secondary-border-color)}@keyframes show-folders{100%{opacity:1}}@media(max-width: 600px){.folders__form{flex-direction:column}.folders__form-button{margin-top:15px}}
.aside{height:100vh;display:flex;flex-direction:column;min-width:320px;background-color:var(--secondary-bg-color);padding:20px 10px 10px;position:relative;z-index:9;transition:.5s}.aside__form .form__field{border-color:var(--primary-border-color)}.aside__form .form__label{background-color:var(--secondary-bg-color)}.aside__button{position:absolute;width:40px;height:40px;background-color:var(--secondary-bg-color);right:-20px;top:50%;margin-top:-20px;border-radius:var(--secondary-border-radius);display:none;align-items:center;justify-content:center}.aside__button svg{width:15px}.aside__button:hover path{fill:var(--primary-svg-color)}@media(max-width: 768px){.aside{position:fixed;left:-310px}.aside__button{display:flex}}.aside--active{left:0}.aside--active .aside__button svg{transform:rotate(180deg)}
.main{display:flex}
:root{--primary-text-color: #FFFFFF;--primary-bg-color: #212832;--secondary-bg-color: #2C343F;--tertiary-bg-color: #543FD3;--danger-bg-color: #E44F4F;--succes-bg-color: #1BC47D;--font-size-xl: 36px;--font-size-lg: 24px;--font-size-md: 18px;--font-size-sm: 14px;--primary-border-radius: 10px;--secondary-border-radius: 20px;--primary-border-color: #212832;--secondary-border-color: #2C343F;--tertiary-border-color: #543FD3;--primary-svg-color: #FFFFFF;--secondary-svg-color: #2C343F}*{margin:0;padding:0;box-sizing:border-box;font-family:"Comfortaa",cursive}*::-webkit-scrollbar{width:0}body{height:100%;background-color:#212832;background-color:var(--primary-bg-color);color:#FFFFFF;color:var(--primary-text-color)}button,input{background:none;border:none;outline:none;cursor:pointer;color:#FFFFFF;color:var(--primary-text-color);transition:.5s}input:-ms-input-placeholder{color:#FFFFFF;color:var(--primary-text-color);font-size:14px;font-size:var(--font-size-sm)}input::placeholder{color:#FFFFFF;color:var(--primary-text-color);font-size:14px;font-size:var(--font-size-sm)}svg,path{transition:.5s}a{text-decoration:none}
